.header{background:linear-gradient(0deg, hsl(36, 100%, 55%) 0%, hsl(196, 83%, 84%) 2%);overflow:hidden;position:relative;top:0;left:0}.menu__list-right{display:flex;align-items:flex-start;justify-content:flex-end}.menu__list{display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap}.menu__item{margin-right:calc(1vw + .1em);margin-bottom:clamp(7px,.5vw,14px)}.menu__item:not(:last-child){margin-right:calc(1vw + .1em)}.menu__link{position:relative;transition:color .3s ease-in-out;color:#000}.menu__link:hover{color:#a9a5a5}.menu__link.active{color:#a9a5a5}.menu__item>a::before{content:"";position:absolute;width:100%;height:3px;background-color:#a9a5a5;bottom:0;left:0;transform-origin:right;transform:scaleX(0);transition:transform .3s ease-in-out}.menu__item>a:hover::before{transform-origin:left;transform:scaleX(1)}.block-search-cart{margin-right:.8rem}.list-right{text-align:center}.list-right__item:not(:last-child){margin-right:calc(1vw + .1em)}.cart-header{display:flex;align-items:center;justify-content:center;position:relative;margin-bottom:1.2rem}.cart-header::after{content:"*курс USD/RUB: 95";position:absolute;color:#000;font-size:.6rem;right:88px;bottom:-8px}.cart-header__icon{position:relative;margin-right:.5rem}.select--header{background-color:rgba(0,0,0,0);border-radius:.3rem;padding:clamp(3px,1vw,5px)}.select--header:active{border:none}.cart-header__quantity-goods{padding:1px 4px;font-weight:700;box-shadow:0 0 0 2px #fff;top:-7px;right:-3px;background:linear-gradient(0deg, #fc8507 0%, #ffa218 100%);border-radius:50px;align-items:center;color:#fff;display:inline-flex;font-size:10px;height:14px;justify-content:center;line-height:13px;min-width:14px;position:absolute;text-align:center}@media(max-width: 500px){.menu__list{justify-content:center}.menu__item{margin-bottom:calc(1vw + .3rem)}}@media(min-width: 320px){.header__wrapper{display:flex;align-items:flex-start;justify-content:space-between;flex-wrap:wrap;color:#fff;width:100%;padding:clamp(7px,4vw,15px);padding-bottom:20px}.header__logo-burger{display:flex;justify-content:space-between;align-items:center;width:100%;margin-bottom:15px}.header__logo{font-size:2rem;font-family:"SuperThing NBP",sans-serif;border-radius:.1em;padding:.15em;background-color:#000;text-align:center;color:#fff;width:3.125rem;height:3.125rem;margin-right:clamp(7px,1vw,20px)}.menu{display:flex;align-items:flex-start;justify-content:center;flex-wrap:wrap;width:100%}.menu__list-left{display:none}.menu__list-left.activ-nav{width:280px;padding:.8rem;line-height:1.5;display:flex;flex-direction:column;align-items:flex-start;justify-content:flex-start;flex-wrap:wrap;max-width:18rem;background-color:var(--form-border-color);border-radius:.3rem;margin-bottom:1.4rem}.menu__list-right{display:flex;gap:1.3rem}}@media(min-width: 603px){.menu__list-left.activ-nav{margin-right:1rem}}@media(min-width: 1000px){#hamburger{display:none}.menu__list-left{display:flex}}