.goods-field{grid-template-columns:1fr 1fr 1fr}.goods-block{display:flex;flex-direction:column;align-items:center;gap:clamp(10px,1vw,12px);padding:clamp(10px,1vw,12px)}.goods-block__imgage-wrap{position:relative;max-width:15rem;min-width:100%}.goods-block__click-bate{position:absolute;right:0;top:0;width:5rem;text-align:center;font-size:.7rem}.goods-block__img{width:100%;height:100%;object-fit:cover}.goods-block__title-coast>p{margin-bottom:clamp(10px,1vw,12px);text-align:center}.goods-block__rating::after{content:"✭";color:#fcc90f;margin-left:4px}@media(min-width: 420px){.goods-field{display:grid;grid-template-columns:1fr;align-items:end;justify-content:center;justify-items:stretch;gap:1rem;flex-wrap:wrap;height:100%}}@media(min-width: 769px){.goods-field{grid-template-columns:1fr 1fr}}@media(min-width: 1000px){.goods-field{grid-template-columns:1fr 1fr 1fr}}